body {
  background-color: #8ecae6;
  padding: 0 24px;
  margin: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  max-height: 100%;
  overflow-y: auto;
}

.App {
  color: #72a24d;
}

form {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5ebe0;
  border-radius: 8px;
  margin-bottom: 10%;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  color: #444;
}

input[type="text"],
input[type="email"],
input[type="url"],
textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: none;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #fff;
  color: #333;
  border: 2px solid #ccc;
}

input[type="number"]{
  width: 70%;
  padding: 12px;
  margin-bottom: 20px;
  border: none;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #fff;
  color: #333;
  border: 2px solid #ccc;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
textarea:focus {
  outline: none;
  border-color: #6bcbef;
}

button[type="submit"] {
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: #615D6C;
  /* background-color: #80b918;  When clicked */
  color: #fff;
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 50%;
  transition: background-color 0.3s ease-in-out;
}

button[type="submit"]:hover {
  color: #555;
  background-color: #ACEDFF;
}


